import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home'
import Projects from '@/components/Projects'
import ProjectPSA from '@/components/ProjectPSA'
import Project21c from '@/components/Project21c'
import ProjectWBG from '@/components/ProjectWBG'
import ProjectGarage from '@/components/ProjectGarage'
import Careers from '@/components/Careers'
import CareersDigitalDesigner from '@/components/CareersDigitalDesigner'
import CareersFrontEndDeveloper from '@/components/CareersFrontEndDeveloper'
import Services from '@/components/Services'
import Company from '@/components/Company'
import Contact from '@/components/Contact'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/project/waterfront-botanical-gardens',
      name: 'Projects: Waterfront Botanical Gardens',
      component: ProjectWBG
    },
    {
      path: '/project/garage-bar',
      name: 'Projects: Garage Bar',
      component: ProjectGarage
    },
    {
      path: '/project/21c',
      name: 'Projects: 21c',
      component: Project21c
    },
    {
      path: '/project/psa',
      name: 'Projects: PSA',
      component: ProjectPSA
    },
    {
      path: '/projects',
      name: 'Projects',
      component: Projects
    },
    {
      path: '/careers',
      name: 'Careers',
      component: Careers
    },
    {
      path: '/careers/digital-designer',
      name: 'CareersDigitalDesigner',
      component: CareersDigitalDesigner
    },
    {
      path: '/careers/front-end-developer',
      name: 'CareersFrontEndDeveloper',
      component: CareersFrontEndDeveloper
    },
    {
      path: '/services',
      name: 'Services',
      component: Services
    },
    {
      path: '/company',
      name: 'Company',
      component: Company
    },
    {
      path: '/contact',
      name: 'contact',
      component: Contact
    },
    {
      path: '/hello-world',
      name: 'hello-world',
      component: HelloWorld
    }
  ]
})



